* {
  box-sizing: border-box;
}

html {
  --bg: #fff;
  --fg: #25282A;
  --gray: #888;
  --royal: #0072CE;
  --blue: #6AD1E3;
  --primary: #D1CCBD;
  --secondary: var(--royal);
  --tertiary: #FF585D;

  background-color: var(--bg);
  color: var(--fg);
  font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Helvetica,Arial,sans-serif;
  font-size: calc(.65em + 0.7vw);
  line-height: 1.3;

  ::selection {
    background: var(--secondary);
    color: var(--bg);
  }

  @media (prefers-color-scheme: dark) {
    --bg: #25282A;
    --fg: #fff;
    --secondary: var(--blue);

    ::selection {
      background: var(--secondary);
      color: var(--fg);
    }
  }
}

body {
  margin: 0 auto;
  padding: 0 1em;
  max-width: 40em;
}

fieldset {
  border: none;
  margin: 0;
  padding: 0;
}

.highlight {
  align-items: center;
  display: flex;
  margin-bottom: 0.5em;
  width: 100%;
  label {
    margin-right: 0.5em;
  }
  input {
    caret-color: var(--secondary);
  }
}

label {
  color: var(--gray);
}

button, .highlight {
  border-radius: 5px;
  border-color: var(--primary);
  border: 0.1em solid var(--primary);
  padding: 0.5em 1em;

  &:hover, &:focus, &:focus-within {
    border-color: var(--secondary);
  }
}

input {
  border: none;
  flex: 1;
  &:read-only {
    color: var(--primary)
  }
}

body {
    background-image: linear-gradient(180deg, #A5A4B0, #D2D2DB);
    background-repeat: no-repeat;
    height: 100vh;
    min-height: 100vh;
    width: 100vw;
    color: #F7F8FD;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: 'MyWebFont';
    text-transform: uppercase;
}

p {
    font-family: 'Roboto Condensed', sans-serif;
    margin-bottom: 0;
}

a {
    color: #F7F8FD;
    text-decoration: none !important;
}

.login-button {
    background: #535164;
    border-radius: 100px;
    height: 106px;
    width: 90%;
    color: #F7F8FD;
    display: flex;
    flex-direction: row;
    margin-bottom: 1em !important;
    padding: 2em;
    margin: 0 auto;
    font-family: 'MyWebFont';
    text-transform: uppercase;
    text-decoration: none;
}

.login-button>img {
    height: 42px;
    width: 42px;
    margin-right: 20px;
}

.login-button>span {
    font-size: 18px;
    line-height: 22px;
}

.permission {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    height: 80vh;
}

.permission>h1 {
    color: #F7F8FD;
    font-size: 24px;
    text-transform: uppercase;
    text-align: center;
}

.permission>div>h6 {
    font-size: 18px;
    text-align: center;
    text-transform: uppercase;
}


