.add-btn {
    height: 60px;
    width: 60px;
    display: block;
    margin: 0 auto;
    position: sticky;
    bottom: 10px;
}

@font-face {
    font-family: 'MyWebFont';
    src: url('../Trocchi-Regular.ttf') format('truetype'), /* Safari, Android, iOS */
}

.btn-small {
    font-family: 'MyWebFont';
    text-transform: uppercase;
    background: #535164;
    color: #F7F8FD !important;
    text-decoration: none;
    padding: 15px 20px;
    border-radius: 30px;
    font-size: 12px;
}

.btn-big {
    font-family: 'MyWebFont';
    text-transform: uppercase;
    background: #535164;
    color: #F7F8FD !important;
    text-decoration: none;
    padding: 20px 76px;
    border-radius: 60px;
    font-size: 18px;
}

.text-circle {
    display: flex;
    justify-content: center;
    margin: 0px;
    background: #F7F8FD;
    border-radius: 200px;
    width: 132px;
    height: 132px;
    color: #535164;
    margin: 0 auto;
    padding: 2em 1em;
    flex-direction: column;
    text-align: center;
}

.bar-graphic {
    height: 30px;
    width: 40vw;
    border-radius: 50px;
    background: #F7F8FD;
    position: relative;
    top: 55px;
    left: -25px;
}

input,
textarea {
    background: #F7F8FD;
    border-radius: 25px;
    border: none;
    width: 80vw;
    padding: 10px 20px;
}

input[type="number"] {
  text-align: center;
  border-bottom: 0.1em solid var(--primary);
  margin: 0 1em;
  width: 4em;
  padding-left: 0.5em;
  &:hover, &:focus {
    border-color: var(--secondary);
  }
}

button, input {
  background: transparent;
  color: inherit;
  cursor: pointer;
  font: inherit;
  outline: none;
}

button {
  position: relative;
  transition: top 50ms;
  &:hover, &:focus {
    top: -1px;
  }
  &:active {
    background: var(--secondary);
    border-color: var(--secondary);
    top: 1px;
  }
}

.is-premium {
  border-left: 0.25em solid var(--secondary);
  padding-left: 0.25em;
  margin-left: -0.5em;
}
